#about{
    display: flex;
    flex-direction:row;
   background-color: rgb(208, 221, 225);
    padding: 3px;
   
}
#about>div{
    width: 50%;
   
    padding: 10px;
    background-color: white;
}
#about>div:nth-child(1)>img{
    width: 300px ;
    height: 300px;
    margin: auto;
}
.home-img{
    border-radius: 50%;
    width: 200px;
    border: 2px solid rgb(255, 53, 53);
}




@media all and (min-width:579px ) and (max-width:880px){
    #about{
    
        flex-direction:column;
    }
    #about>div{
        width: 100%;
      
    }
}
@media all and (min-width:150px ) and (max-width:580px){
    #about{
    
        flex-direction:column;
    }
    #about>div{
        width: 100%;
      
    }
 
    #about>div:nth-child(1)>img{
        width: 200px ;
        height: 200px;
        margin: auto;
    }
    
}